@import '../../styles/variables.scss';

.cartPage {
    &__title {
        font-size: 20px;
        font-weight: 600;
        line-height: 24px;
        color: $colorBlackPrimary;
        margin-bottom: 13px;
    }
    &__content {
        display: flex;
        justify-content: space-between;
        flex-wrap: wrap;
        gap: 20px;
        margin-bottom: 30px;
    }
    &__cards {
        display: flex;
        flex-direction: column;
        gap: 20px;
        max-width: 633px;
        width: 100%;
        margin-bottom: 20px;
    }
}

.checkout {
    max-width: 350px;
    width: 100%;
    height: 120px;
    border-radius: 30px;
    box-shadow: 0px 0px 20px 0px rgba(0, 0, 0, 0.1);
    background: $colorWhiteDefault;
    &__total {
        padding: 21px 16px 15px 21px;
        display: flex;
        justify-content: space-between;
        align-items: center;
        font-size: 15px;
        font-weight: 600;
        line-height: 18px;
        color: $colorBlackDefault;
    }
    &__btn {
        width: 100%;
        padding: 22px;
        border-radius: 20px;
        box-shadow: 0px 0px 20px 0px rgba(0, 0, 0, 0.25);
        background: rgb(16, 16, 16);
        color: $colorWhiteDefault;
        font-size: 17px;
        font-weight: 600;
        line-height: 21px;
        text-align: center;
        transform: translate(0, 1px);
        &:hover {
            transition: .3s;
            background-color: $colorOrangePrice;
        }
    }
}