.footer{min-height:150px;padding:30px;background-color:#fff;border-radius:30px 30px 0px 0px;box-shadow:0px 0px 20px 0px rgba(0,0,0,.1);display:flex;flex-wrap:wrap;gap:15px;justify-content:space-between;align-items:start;font-size:17px;font-weight:400;line-height:21px;color:#000}.footer__nav{max-width:427px;width:100%}.footer__nav ul{max-height:103px;height:100%;list-style-type:none;display:flex;flex-direction:column;flex-wrap:wrap;gap:10px;justify-content:space-between;align-items:stretch;align-content:space-between}.footer__nav ul li:last-child{margin-top:38px}.footer__nav ul li:not(:last-child):hover{transition:color .3s;color:#ffa542}.footer__langChoiсe{display:flex;align-items:center;gap:15px;font-size:15px;font-weight:500;line-height:18px}.footer__langChoiсe>button:nth-child(3){font-weight:700;color:#ffa542}.footer__langChoiсe button:hover{transition:color .3s;color:#ffa542}.footer__iconLang{font-size:17px;color:#838383}.footer__socials{display:flex;gap:18px}.footer__icon{font-size:30px}.footer__icon:hover{transition:color .3s;color:#ffa542;transform:scale(1.1)}@media(max-width: 425px){.footer{justify-content:center}.footer__nav ul{align-content:center;max-height:inherit}}